import PropTypes from 'prop-types';
import Grid from '@mui/material/Grid';
import { TopBarLink } from './utils';

import About from '../about';
import Contact from '../contact';

export function AboutLink({ wdth, cb }) {
  return (
    <TopBarLink text="About" wdth={wdth} cb={cb}>
      <About />
    </TopBarLink>
  );
}

export function ContactUsLink({ wdth }) {
  return (
    <TopBarLink text="Contact Us" wdth={wdth}>
      <Contact />
    </TopBarLink>
  );
}

export function MiscLinks() {
  return (
    <Grid item sx={{ paddingTop: '0.7rem' }}>
      <AboutLink wdth="lg" />
      <ContactUsLink wdth="xs" />
    </Grid>
  );
}

ContactUsLink.propTypes = {
  wdth: PropTypes.string.isRequired,
};

AboutLink.defaultProps = {
  cb: () => {},
};

AboutLink.propTypes = {
  wdth: PropTypes.string.isRequired,
  cb: PropTypes.func,
};
