import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Dialog from '@mui/material/Dialog';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import Divider from '@mui/material/Divider';
import LinearProgress from '@mui/material/LinearProgress';
import CircularProgress from '@mui/material/CircularProgress';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';

function LinearProgressWithLabel({ value }) {
  return (
    <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
      <Box sx={{ width: '100%', mr: 1 }}>
        <LinearProgress variant="determinate" value={value} />
      </Box>
      <Box sx={{ minWidth: 35 }}>
        <Typography variant="body2" color="text.secondary">
          {`${Math.round(
            value,
          )}%`}
        </Typography>
      </Box>
    </Box>
  );
}

function MessageProgress({ message, done }) {
  const showStatus = done ? (
    <Typography color="success.main">Done</Typography>
  ) : (
    <CircularProgress size={30} />
  );

  return (
    <Box sx={{ display: 'flex', alignItems: 'space-between' }}>
      <Box sx={{ width: '100%', mr: 1 }}>
        <Typography>{message}</Typography>
      </Box>
      <Box sx={{ minWidth: 35 }}>
        {showStatus}
      </Box>
    </Box>

  );
}

export default function CreateMemoryProgress({
  open, progress, messages,
}) {
  const [counter, setCounter] = useState(0);
  useEffect(() => {
    let timerId;
    if (counter > 0) {
      timerId = setTimeout(() => setCounter(counter - 1), 1000);
    }
    return () => clearTimeout(timerId);
  }, [counter]);

  useEffect(() => {
    if (progress === 100) setCounter(10);
  }, [progress]);

  const status = (progress === 100) ? (
    <>
      <Box sx={{
        display: 'flex', justifyContent: 'center', alignItems: 'center', m: 2,
      }}
      >
        <CheckCircleOutlineIcon color="success" fontSize="large" sx={{ mr: 2 }} />
        <Typography variant="h5" color="primary.main">Memory Created Successfully</Typography>
      </Box>

      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        <Box>
          <Typography>Navigating to memories page in </Typography>
        </Box>
        <Box sx={{ mx: 1 }}>
          <Typography variant="h6" color="success.main">
            {counter}
          </Typography>
        </Box>
        <Box>
          <Typography>seconds</Typography>
        </Box>
      </Box>
    </>
  ) : null;

  return (
    <Dialog
      open={open}
      fullWidth
      sx={{
        p: 1,
      }}

    >
      <Card sx={{ maxWidth: 1, p: 1 }}>
        <CardHeader title="Creating Memory" sx={{ textAlign: 'center' }} />
        <Divider />
        <CardContent>
          <LinearProgressWithLabel value={progress} />
          {messages.map((mesg) => (
            <MessageProgress key={mesg.message} message={mesg.message} done={mesg.done} />
          ))}
          {status}
        </CardContent>
      </Card>
    </Dialog>

  );
}

LinearProgressWithLabel.propTypes = {
  value: PropTypes.number.isRequired,
};

MessageProgress.propTypes = {
  message: PropTypes.string.isRequired,
  done: PropTypes.bool.isRequired,
};

CreateMemoryProgress.defaultProps = {
  messages: [],
};

CreateMemoryProgress.propTypes = {
  open: PropTypes.bool.isRequired,
  progress: PropTypes.number.isRequired,
  messages: PropTypes.arrayOf(PropTypes.exact(
    {
      message: PropTypes.string,
      done: PropTypes.bool,
    },
  )),
};
