import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';
import Grid from '@mui/material/Grid';
import MemoryThumbnail from '../MemoryThumbnail';

import CreateMemoryItem from './CreateMemoryItem';
import LoadMore from './LoadMore';

export default function GridView({ memories, handleMoreClick, nextToken }) {
  const navigate = useNavigate();
  const sortedMemories = memories.sort((a, b) => (a.memory_id > b.memory_id ? 1 : -1));

  const handleClick = (memDetails) => {
    navigate(`/memory/${memDetails.memory_id}`, { state: memDetails });
  };

  return (
    <Grid container sx={{ bgcolor: 'button.main', py: 1 }} justifyContent="left" alignItems="flex-start">
      <CreateMemoryItem />
      {sortedMemories.map((memory) => (
        <Grid
          item
          key={memory.title}
          lg={3}
          md={4}
          xs={6}
          sx={{ p: 1 }}
        >
          <MemoryThumbnail
            title={memory.title}
            date={memory.date}
            thumbnailImage={memory.image_url}
            handleClick={() => handleClick(memory)}
          />
        </Grid>
      ))}
      { nextToken && (<LoadMore handleMoreClick={handleMoreClick} />) }
    </Grid>
  );
}

GridView.defaultProps = {
  memories: [],
};

GridView.propTypes = {
  memories: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.number.isRequired,
      title: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired,
      imageDetails: PropTypes.arrayOf(PropTypes.shape({
        url: PropTypes.string,
        name: PropTypes.string,
        size: PropTypes.number,
        width: PropTypes.number,
        height: PropTypes.number,
      })),
      totalSize: PropTypes.number.isRequired,
      thumbnailImage: PropTypes.string,
    }),
  ),
  nextToken: PropTypes.string.isRequired,
  handleMoreClick: PropTypes.func.isRequired,
};
