import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

export function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scroll(0, 0);
  }, [pathname]);

  return null;
}

export function MutedText({ children }) {
  return (
    <Typography sx={{ paddingX: 2, color: 'muted.main', display: 'inline' }}>
      {children}
    </Typography>
  );
}

export function LanguageChanger({ language, setEnglish, setBengali }) {
  return (
    <Box sx={{ display: 'flex', justifyContent: 'center', p: 2 }}>
      <ToggleButtonGroup
        exclusive
        value={language}
        aria-label="language change"
        align="center"
        color="primary"
      >
        <ToggleButton
          value="english"
          onClick={setEnglish}
          sx={{ border: 2, borderColor: 'primary.main' }}
        >
          <Typography variant="h6">English</Typography>
        </ToggleButton>
        <ToggleButton
          value="bengali"
          onClick={setBengali}
          sx={{ border: 2, borderColor: 'primary.main' }}
        >
          <Typography variant="h6"></Typography>
        </ToggleButton>
      </ToggleButtonGroup>
    </Box>
  );
}

MutedText.propTypes = {
  children: PropTypes.node.isRequired,
};

LanguageChanger.propTypes = {
  language: PropTypes.string.isRequired,
  setEnglish: PropTypes.func.isRequired,
  setBengali: PropTypes.func.isRequired,
};
