import { useForm, ValidationError } from '@formspree/react';
import PropTypes from 'prop-types';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Dialog from '@mui/material/Dialog';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import { alpha } from '@mui/material/styles';

function CloseDialogIcon({ size, handleClick }) {
  return (
    <IconButton
      size={size}
      sx={{
        color: 'black',
        bgcolor: alpha('#fff', 0.4),
        position: 'absolute',
        top: 6,
        right: 8,
      }}
      onClick={handleClick}
    >
      <CloseIcon size={size} />
    </IconButton>
  );
}

function EmailDialog({ open, children, handleClose }) {
  return (
    <Dialog
      open={open}
      sx={{
        '& .MuiDialog-paper': {
          m: 0,
        },
      }}
    >
      <CloseDialogIcon handleClick={handleClose} />
      <Typography
        variant="h5"
        sx={{ p: 1, bgcolor: 'primary.main', color: 'button.main' }}
      >
        Get Notified
      </Typography>
      <Divider />
      {children}
    </Dialog>
  );
}

function EmailForm({ open, handleClose }) {
  const [state, handleSubmit] = useForm('mjvjplkk');

  return state.succeeded ? (
    <EmailDialog open={open} handleClose={handleClose}>
      <Typography variant="h6" sx={{ m: 1 }}>
        Thanks!!! Will keep you updated on our progress.
      </Typography>
    </EmailDialog>
  ) : (
    <EmailDialog open={open} handleClose={handleClose}>
      <Box
        component="form"
        onSubmit={handleSubmit}
        sx={{
          m: 1,
        }}
      >
        <TextField
          required
          label="Email"
          id="email"
          type="email"
          name="email"
          sx={{
            borderRadius: '5px 0 0 5px',
            '& .MuiOutlinedInput-root': {
              borderRadius: 'inherit',
              backgroundColor: 'button.main',
            },
          }}
        />
        <Button
          type="submit"
          variant="contained"
          disabled={state.submitting}
          sx={{
            py: 2,
            borderRadius: '0 5px 5px 0',
          }}
        >
          <Typography color="#fff">Notify Me</Typography>
        </Button>
      </Box>
      <ValidationError prefix="Email" field="email" errors={state.errors} />
    </EmailDialog>
  );
}

EmailForm.propTypes = {
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
};

CloseDialogIcon.defaultProps = {
  size: 'small',
};

CloseDialogIcon.propTypes = {
  handleClick: PropTypes.func.isRequired,
  size: PropTypes.string,
};

EmailDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
  children: PropTypes.node.isRequired,
};
export default EmailForm;
