import { useParams } from 'react-router-dom';
import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import { EditorState, convertFromRaw } from 'draft-js';

import api from '../Api';
import BasePage from '../base_page';
import MemoryContent from './MemoryContent';
import MemoryContentLoading from './MemoryContentLoading';

async function getMemory(
  bucket,
  memoryId,
  handleMemoryCallback,
) {
  try {
    const memoryData = await api.viewMemory(bucket, memoryId);
    handleMemoryCallback(memoryData);
  } catch (error) {
    console.log(error);
  }
}

export default function ViewMemory({ bucket }) {
  const params = useParams();
  const { memoryId } = params;
  const [memory, setMemory] = useState();

  const handleMemory = (mem) => setMemory(mem);

  useEffect(() => {
    getMemory(bucket, memoryId, handleMemory);
  }, []);

  return memory ? (
    <BasePage>
      <MemoryContent
        title={memory.title}
        date={memory.date}
        images={memory.pictures.photos}
        editorState={EditorState.createWithContent(convertFromRaw(memory.contentState))}
      />
    </BasePage>
  ) : (
    <BasePage>
      <MemoryContentLoading />
    </BasePage>
  );
}

ViewMemory.propTypes = {
  bucket: PropTypes.string.isRequired,
};
