const get = (url, params = {}) => {
  let newUrl = url;
  const options = { method: 'GET', headers: new Headers({ 'content-type': 'application/json' }) };
  newUrl += `?${(new URLSearchParams(params)).toString()}`;
  return fetch(newUrl, options).then((response) => response.json());
};

function getObjectUrl(bucket, key, method) {
  try {
    const API_URL = '/api/create-presigned-url';
    const params = { key, bucket, method };
    return get(API_URL, params).then((response) => {
      if (response.Error) {
        console.log(response.Error);
        return '';
      }
      return response.URL;
    });
  } catch (error) {
    return error;
  }
}

async function validateDetails(bucket, details) {
  try {
    const API_URL = '/api/validate-details';
    const params = { bucket, details };
    return get(API_URL, params);
  } catch (error) {
    console.log(error);
    return error;
  }
}

async function upload(bucket, key, body) {
  try {
    const putURL = await getObjectUrl(bucket, key, 'PUT');
    const params = { body, method: 'PUT' };
    const resp = await fetch(putURL, params).then((response) => response);
    return resp;
  } catch (error) {
    console.log(error);
    return error;
  }
}

function uploadMultipleImages(bucket, paramList, incrementImageCount) {
  try {
    return paramList.map(async (params) => {
      const resp = await upload(bucket, params.Key, params.Body);
      incrementImageCount();
      return resp;
    });
  } catch (error) {
    console.log(error);
    return error;
  }
}

function getS3Key(bucket, key) {
  try {
    const API_URL = '/api/get-details';
    const params = { bucket, key };
    return get(API_URL, params);
  } catch (error) {
    return error;
  }
}

function getMemoryDetails(bucket, keys) {
  try {
    const API_URL = '/api/get-details';
    const params = { bucket };
    return keys.map((key) => {
      params.memory_id = key;
      return get(API_URL, params);
    });
  } catch (error) {
    return error;
  }
}

function getMemoryList(bucket, nextToken) {
  try {
    const SERVER_URL = '/api/get-names';
    const params = { bucket };

    if (nextToken) {
      params.next_token = nextToken;
    }

    return get(SERVER_URL, params);
  } catch (error) {
    return error;
  }
}

function getMemories(bucket, nextToken) {
  try {
    const SERVER_URL = '/api/get-memories';
    const params = { bucket };

    if (nextToken) {
      params.next_token = nextToken;
    }

    return get(SERVER_URL, params);
  } catch (error) {
    return error;
  }
}

function viewMemory(bucket, memoryKey) {
  try {
    const SERVER_URL = '/api/view-memory';
    const params = { bucket, memory_id: memoryKey };

    return get(SERVER_URL, params);
  } catch (error) {
    return error;
  }
}

function processMemory(bucket, memoryKey) {
  try {
    const SERVER_URL = '/api/process';
    const params = { bucket, memory_id: memoryKey };

    return get(SERVER_URL, params);
  } catch (error) {
    return error;
  }
}

export default {
  getObjectUrl,
  validateDetails,
  upload,
  uploadMultipleImages,
  getS3Key,
  getMemories,
  viewMemory,
  getMemoryDetails,
  getMemoryList,
  processMemory,
};
