import api from '../Api';

function UploadImages(bucket, memoryKey, title, imageFiles, incrementImageCount) {
  const paramList = imageFiles.map((imgFile) => (
    {
      Key: ['memories', memoryKey, 'images', imgFile.name].join('/'),
      Body: imgFile,
      ContentType: imgFile.type,
    }
  ));
  const uploadPromises = api.uploadMultipleImages(bucket, paramList, incrementImageCount);
  return Promise.allSettled(uploadPromises);
}

async function ValidateDetails(bucket, memoryDetails) {
  const detailsJSON = JSON.stringify(memoryDetails);
  const validateResp = await api.validateDetails(bucket, detailsJSON);
  return (validateResp && validateResp.Valid === true);
}

async function UploadDetails(bucket, memoryDetails) {
  const detailsJSON = JSON.stringify(memoryDetails);
  const memoryDetailsKey = ['memories', memoryDetails.key, 'details.json'].join('/');
  const uploadResp = await api.upload(bucket, memoryDetailsKey, detailsJSON);
  return (uploadResp && uploadResp.ok === true);
}

const InitializeMemory = async (
  bucket,
  memoryDetails,
  handleProgress,
  updateMessageStatus,
  addMessage,
) => {
  handleProgress(0);
  addMessage('Initializing memory', false);
  const valid = await ValidateDetails(bucket, memoryDetails);
  let initRes;
  if (valid === true) {
    initRes = await UploadDetails(bucket, memoryDetails);
  }
  updateMessageStatus('Initializing Memory', initRes);
  handleProgress(10);
  return initRes;
};

const HandleImages = async (
  bucket,
  memoryKey,
  title,
  imageFiles,
  incrementImageCount,
  handleProgress,
) => {
  const imagesResults = await UploadImages(
    bucket,
    memoryKey,
    title,
    imageFiles,
    incrementImageCount,
  );
  handleProgress(95);
  return imagesResults;
};

const CreateMemory = async (
  bucket,
  memoryDetails,
  handleProgress,
  updateMessageStatus,
  addMessage,
  imageFiles,
  incrementImageCount,
) => {
  // Validate details and upload if valid
  const initMemResults = await InitializeMemory(
    bucket,
    memoryDetails,
    handleProgress,
    updateMessageStatus,
    addMessage,
  );
  console.log('Initialize Memory completed', initMemResults);

  let imagesResults;
  if (initMemResults === true) {
    // Upload original images
    imagesResults = await HandleImages(
      bucket,
      memoryDetails.key,
      memoryDetails.title,
      imageFiles,
      incrementImageCount,
      handleProgress,
    );
    console.log('Imgages uploaded', imagesResults);
    for (let i = 0; i < imagesResults.length; i += 1) {
      if ((imagesResults[i].status === 'rejected') || (imagesResults[i].value.ok !== true)) {
        return false;
      }
    }

    return true;
  }

  return false;
};

export default { CreateMemory };
