import PropTypes from 'prop-types';
import PhotoAlbum from 'react-photo-album';
import { useState, useEffect } from 'react';
import { EditorState } from 'draft-js';
import MemoryStory from './MemoryStory';
import MemoryContentStructure from './MemoryContentStructure';
import MemoryCarousel from './MemoryCarousel';
import './memory.css';

export default function MemoryContent({
  title, date, editorState, images,
}) {
  const [open, setOpen] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);
  const [layout, setLayout] = useState(null);
  const maxSteps = images.length;

  useEffect(() => {
    let twidth = 0;
    let theight = 0;
    images.forEach(({ height, width }) => {
      if (height > width) theight += 1;
      else twidth += 1;
    });
    if (twidth > theight) setLayout('rows');
    else setLayout('columns');
  }, [images]);

  const handleClose = () => setOpen(false);

  const handleBack = () => {
    setActiveIndex((prevActiveStep) => (prevActiveStep - 1 + maxSteps) % maxSteps);
  };

  const handleNext = () => {
    setActiveIndex((prevActiveStep) => (prevActiveStep + 1) % maxSteps);
  };

  const handlePhotoClick = (photo, event, index) => {
    setOpen(true);
    setActiveIndex(index);
  };

  const photoLayout = (
    <PhotoAlbum
      layout={layout}
      photos={images}
      onClick={handlePhotoClick}
      spacing={2}
    />
  );

  const storyLayout = <MemoryStory editorState={editorState} />;
  //  console.log('Memory Content', new Date(), images, photoLayout);
  return (
    <>
      <MemoryContentStructure
        header={title}
        subheader={date}
        photoLayout={photoLayout}
        storyLayout={storyLayout}
      />
      <MemoryCarousel
        open={open}
        title={title}
        date={date}
        images={images}
        index={activeIndex}
        handleNext={handleNext}
        handleBack={handleBack}
        handleClose={handleClose}
      />
    </>
  );
}

MemoryContent.defaultProps = {
  title: 'Memory Title',
  date: 'Memory Date',
  editorState: EditorState.createWithText('Memory Text'),
};

MemoryContent.propTypes = {
  title: PropTypes.string,
  date: PropTypes.string,
  editorState: PropTypes.instanceOf(EditorState),
  images: PropTypes.arrayOf(PropTypes.shape({
    src: PropTypes.string,
    width: PropTypes.number,
    height: PropTypes.number,
  })).isRequired,
};
