import PropTypes from 'prop-types';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';

export default function MemoryContentStructure({
  header, subheader, photoLayout, storyLayout,
}) {
  let className;
  const theme = useTheme();
  const lgdown = useMediaQuery(theme.breakpoints.down('lg'));
  if (lgdown) className = '';
  else className = 'vertical-scroll';

  return (
    <Card
      sx={{
        boxShadow: 10,
        bgcolor: 'button.main',
        p: 1,
      }}
    >
      <CardHeader title={header} subheader={subheader} />
      <Divider />
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-start',
          flexDirection: {
            lg: 'row',
            xs: 'column',
          },
          height: {
            lg: '72vh',
          },
          my: 1,
        }}
      >
        <Box
          sx={{
            width: {
              lg: '70%',
              xs: '100%',
            },
            overflowY: 'scroll',
            borderRadius: 5,
          }}
          className={className}
        >
          {photoLayout}
        </Box>
        <Box
          sx={{
            width: { lg: '30%', sx: '100%' },
            overflowY: 'scroll',
            mt: {
              lg: 0,
              xs: 2,
            },
            p: {
              lg: 1,
            },
            px: 1,
          }}
          className={className}
        >
          {storyLayout}
        </Box>
      </Box>
    </Card>
  );
}

MemoryContentStructure.propTypes = {
  header: PropTypes.oneOfType([PropTypes.string, PropTypes.element]).isRequired,
  subheader: PropTypes.oneOfType([PropTypes.string, PropTypes.element]).isRequired,
  photoLayout: PropTypes.element.isRequired,
  storyLayout: PropTypes.element.isRequired,
};
